<template>
  <div class="page">
    <div class="bg">
      <img :src="bginfo.pic" v-if="bginfo.pic">
      <video :src="bginfo.video" v-else preload="auto" autoplay loop muted @click.stop="openvo"></video>
      <div class="text">
        <span>{{bginfo.picname}}</span>
        <span>{{bginfo.picadd}}</span>
      </div>
    </div>
    <div class="content" style="flex-direction: column">
      <div class="top">
        <router-link tag="a" :to="'/articles/'+articles.id">
          <div class="time">{{articles.mtime}}</div>
          <div class="title">{{articles.title}}</div>
          <div class="img"><img :src="articles.tpic"></div>
        </router-link>
      </div>
      <div class="top">
        <router-link tag="a" :to="'/lifes/'+lifes.id">
          <div class="time">{{lifes.time}}</div>
          <div class="title">{{lifes.title}}</div>
          <div class="img"><img :src="lifes.tpic"></div>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
//import {list} from '@/api/api'

export default {
    name: 'index',
    data() {
        return {
            sw: window.innerWidth,
            sh: window.innerHeight,
            bginfo: {},
            articles: {},
            lifes: {}

        }
    },
    props: {
        dataList: {
            type: Array,
            default() {
                return []
            }
        }
    },
    created() {
        if (this.$store.state.articles.bgimg.length>0) {
            let xh = Math.floor(Math.random() * this.$store.state.articles.bgimg.length)
            this.bginfo = this.$store.state.articles.bgimg[xh]
        } else {
            let data = {
                app_key: '1D5AF16FD7D90EE3C5CF70D088249505',
                model_name: 'bgimg',
                where: ["xs=0"],
                order: ["id DESC"]
            }
            this.$store.dispatch('articles/getArticles', data).then((rs) => {
                let xh = Math.floor(Math.random() * rs.length)
                this.bginfo=rs[xh]
            })

        }
        if (this.$store.state.articles.articles.length>0) {
            this.articles = this.$store.state.articles.articles[0]
        } else {
            let data = {
                app_key: '1D5AF16FD7D90EE3C5CF70D088249505',
                model_name: 'article',
                perpage: 1000,
                order: ["mtime DESC", "id DESC"]
            }
            this.$store.dispatch('articles/getArticles', data).then((rs) => {
                this.articles = rs[0]
            })

        }
        if (this.$store.state.articles.lifes.length>0) {
            this.lifes = this.$store.state.articles.lifes[0]
        } else {
            let data = {
                app_key: '1D5AF16FD7D90EE3C5CF70D088249505',
                model_name: 'life',
                perpage: 1000,
                order: ["mtime DESC", "id DESC"]
            }
            this.$store.dispatch('articles/getArticles', data).then((rs) => {
                this.lifes = rs[0]
            })

        }
    },
    methods: {
        openvo() {
            let target = event.currentTarget
            target.muted = false
        }
    },
    computed: {}
}
</script>

